.app {
  text-align: center;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #6dd5fa, #2980b9);
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}

.header {
  height: 10%;
}

.top-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 0.6rem 0.6rem;
  z-index: 10;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.date-info {
  text-align: center;
  flex-grow: 1;
}

.date-text {
  font-size: 1.4rem;
  font-weight: bold;
  text-decoration: underline;
}

.week-number {
  font-size: 1rem;
  margin-top: 0.2rem;
  display: block;
}

.content {
  margin-top: auto;
  margin-bottom: auto;
  text-align: center;
}

.content h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.content p {
  font-size: 1.2rem;
}

button {
  background-color: rgba(0, 0, 0, 0.55);
  border: none;
  border-radius: 50%;
  margin: 0 5%;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

button svg {
  color: #ffffff;
  width: 1.5rem;
  height: 1.5rem;
}


